*{margin:0;padding:0}body{width:100%;background:#deb887}.account_form{width:100%;height:88vh;display:flex;justify-content:center;align-items:center;overflow:hidden;font-family:"Montserrat"}.form_box{width:650px;height:550px;background:rgba(24,14,1,.541);border:2px solid rgba(255,255,255,.5);border-radius:20px;backdrop-filter:blur(15px);display:flex;justify-content:center;align-items:center}.form_value{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}form{width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}form>h2{font-size:2em;color:#000;text-align:center}.inputbox{position:relative;margin:30px 0;width:80%;border-bottom:2px solid #fff}.inputbox label{position:absolute;top:40%;left:5px;transform:translateY(-50%);font-size:1.3em;pointer-events:none;transition:.5s;color:#fff}.inputbox input{width:100%;height:50px;background:rgba(0,0,0,0);border:none;outline:none;padding:0 35px 0 5px;color:#fff;font-size:1.1em}.inputbox input:focus~label,.inputbox input:valid~label{top:-10px}.inputbox .icon{position:absolute;right:8px;width:35px;transform:scale(2);top:11px}.toggle_password{position:absolute;right:-25px;top:25%;transform:scale(1.4);cursor:pointer;color:#fff}.icon_visibility{width:35px}.forget{width:80%;margin:-15px 0 15px;font-size:.9em;color:#fff;display:flex;align-items:center;justify-content:space-between}.forget .forget_checkbox{display:flex;flex-direction:row;width:210px}.forget .forget_password a{text-align:center;color:#fff;text-decoration:none}.forget .forget_password a:hover{text-decoration:underline}.forget input{appearance:none;-webkit-appearance:none;-moz-appearance:none;position:relative;width:1.5em;height:1.5em;border:1px solid #adb5bd;border-radius:.25em;outline:none;margin-right:10px}.forget input:checked::before{content:"✔";width:100%;background-color:#0b76ef;border-color:#0b76ef;border-radius:.25em;display:flex;justify-content:center;color:#fff;font-size:1em;position:absolute}.forget input:focus+label::before{box-shadow:0 0 0 .2rem rgba(0,123,255,.25)}.forget label{width:70%;position:relative;display:flex;align-items:center;justify-content:space-between}.btn_login{width:80%;height:45px;border-radius:40px;background:#000;color:#fff;border:none;outline:none;cursor:pointer;font-size:1em;font-weight:600;transition:.3s}.btn_login:hover{background:#fff;color:#000;font-size:larger;transition:.3s}.register{font-size:.9em;color:#fff;text-align:center;margin:25px 0 10px}.register p a{text-decoration:none;color:#fff;font-weight:600}.register p a:hover{text-decoration:underline}@media screen and (max-width: 840px){.form_box{width:90%}}